import React, { Component } from "react"
import {
  Col,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap"
import styled from "styled-components"

const WrapLayout = styled.div`
  min-height: 70vh;
  & .nav-item-active {
    background: #033c73;
    color: #fff;
    border-radius: 0.3rem 0.3rem 0 0;
    transition: 0;
    border: 0;
    & .nav-link:hover {
      color: #fff !important;
    }
  }
`
class MUI_Api_Client extends Component {
  constructor(props) {
    super(props)
    this.state = {
      currentActiveTab: "1",
      data: this.props.data,
    }
  }
  toggle = tab => {
    if (this.state.currentActiveTab !== tab)
      this.setState({ currentActiveTab: tab })
  }
  render() {
    console.log(this.state.data)
    return (
      <WrapLayout>
        <div className="row">
          <div className="col-7">
            <h4>Request</h4>
            <Nav tabs>
              <NavItem
                className={`${
                  this.state.currentActiveTab === "1" ? "nav-item-active" : ""
                }`}
              >
                <NavLink
                  onClick={() => {
                    this.toggle("1")
                  }}
                >
                  Headers
                </NavLink>
              </NavItem>
              <NavItem
                className={`${
                  this.state.currentActiveTab === "2" ? "nav-item-active" : ""
                }`}
              >
                <NavLink
                  onClick={() => {
                    this.toggle("2")
                  }}
                >
                  Parameters
                </NavLink>
              </NavItem>
              <NavItem
                className={`${
                  this.state.currentActiveTab === "3" ? "nav-item-active" : ""
                }`}
              >
                <NavLink
                  onClick={() => {
                    this.toggle("3")
                  }}
                >
                  Body
                </NavLink>
              </NavItem>
              <NavItem
                className={`${
                  this.state.currentActiveTab === "4" ? "nav-item-active" : ""
                }`}
              >
                <NavLink
                  onClick={() => {
                    this.toggle("4")
                  }}
                >
                  Authorization
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={this.state.currentActiveTab}>
              <TabPane tabId="1">
                <h5>Http Headers</h5>
              </TabPane>
              <TabPane tabId="2">
                <h5>Parameters</h5>
              </TabPane>
              <TabPane tabId="3">
                <h5>Body</h5>
              </TabPane>
              <TabPane tabId="4">
                <h5>Authorization</h5>
              </TabPane>
            </TabContent>
          </div>
          <div className="col-5">
            <h4>Response</h4>
          </div>
        </div>
      </WrapLayout>
    )
  }
}
export default MUI_Api_Client
